  width: 40%;
  padding: 2px;
  position: absolute;
  top: 80%;
  left: 32%;
}

.todoname p {
  color: #777a92;
  margin-left: 10%;
  font-size: 1.2rem;
  font-weight: 400;
}

.list {
  border-bottom: 1px solid var(--primary-darkGrayish-Blue);
